<template>
  <div class="searchdetail">
    <van-tabs
      v-model="active"
      title-inactive-color="#ccc"
      title-active-color="#ee0a24"
      swipeable
      animated
      sticky
      @change="getsearchdata"
    >
      <van-tab
        v-for="(item, index) in singertype"
        :title="item.title"
        :key="index"
      >
        <div class="types" v-show="active == 0">
          <div class="song-list-box">
            <div class="song-list-top">
              <div class="song-list-left">
                <div class="song-list-img-box">
                  <img class="auto-img" src="../../assets/images/mv-play.png" />
                </div>
                <div class="song-list-playall">
                  播放全部<span>共{{ getdatas.length }} 首</span>
                </div>
              </div>
              <div class="song-list-collect">更多</div>
            </div>
            <div
              class="song-list-top"
              v-for="(item, index) in getdatas"
              :key="index"
              @click="setStateCount(item)"
            >
              <div class="song-list-left">
                <div class="song-list-img-box">{{ index + 1 }}</div>
                <div class="song-list-playall">
                  <div class="song-list-content van-ellipsis">
                    {{ item.name }}
                  </div>
                  <div class="song-list-detail dq van-ellipsis">
                    <div
                      class="mlr2"
                      v-for="(itn, ind) in item.artists"
                      :key="ind"
                    >
                      {{ itn.name }}
                    </div>
                  </div>
                </div>
              </div>
              <div class="playbtn"><van-icon name="play-circle-o" /></div>
            </div>
          </div>
        </div>
        <div class="types" v-show="active == 1">
          <div
            class="recommend-item"
            v-for="(item, index) in getdatas"
            :key="index"
            @click="goSingerDetail(item)"
          >
            <div class="recommend-img-box">
              <img class="auto-img" :src="item.img1v1Url" />
            </div>
            <div class="recommend-content">
              <div class="recommend-name van-ellipsis">{{ item.name }}</div>
              <div class="recommend-text">
                <div class="album van-ellipsis">专辑 {{ item.albumSize }}</div>
                <div class="single van-ellipsis">MV： {{ item.mvSize }}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="types" v-show="active == 2">
          <div v-if="isShow">
            <div class="song-list-box">
              <div
                class="recommend-item"
                v-for="(item, index) in getdatas"
                :key="index"
                @click="goSongAlbumDetail(item)"
              >
                <div class="recommend-img-box">
                  <img class="auto-img" :src="item.blurPicUrl" />
                </div>
                <div class="recommend-content">
                  <div class="recommend-name van-ellipsis">{{ item.name }}</div>
                  <div class="recommend-text van-ellipsis">
                    <div class="album mr van-ellipsis">
                      {{
                        item.publishTime | getTemplateTimeByDate("YYYY.MM.dd")
                      }}
                    </div>
                    <!-- <div class="album ">item.publishTime | getTemplateTimeByDate('YYYY.MM.dd')</div> -->
                    <div class="album van-ellipsis">歌曲：{{ item.size }}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-else>
            <van-empty description="暂无数据" />
          </div>
        </div>
        <div class="types" v-show="active == 3">
          <div v-if="isShow">
            <div class="song-list-box">
              <div
                class="recommend-item"
                v-for="(item, index) in getdatas"
                :key="index"
                @click="goSongListDetail(item)"
              >
                <div class="recommend-img-box">
                  <img class="auto-img" :src="item.coverImgUrl" />
                </div>
                <div class="recommend-content">
                  <div class="recommend-name van-ellipsis">{{ item.name }}</div>
                  <div class="recommend-text van-ellipsis">
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-else>
            <van-empty description="暂无数据" />
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import "../../assets/less/searchdedtail.less";

export default {
  data() {
    return {
      active: 0,
      searchactive: "",
      singertype: [
        {
          title: "单曲",
        },
        {
          title: "歌手",
        },
        {
          title: "专辑",
        },
        {
          title: "歌单",
        },
      ],
      getdatas: [],
      isShow:true
    };
  },
  beforeRouteUpdate(to, from, next) {
    // 做个判断歌曲切换了才重新请求数据
    if (to.name === from.name && to.query.wkey !== from.query.wkey) {
      this.searchactive = to.query.wkey;
      this.getsearchdata();
    }
    next();
  },
  created() {
    this.searchactive = this.$route.query.wkey;
    this.getsearchdata();
  },
  filters: {
    million: function (value) {
      //播放量数值处理
      let num;
      if (value > 99999999) {
        num = Math.floor(value / 10000000) / 10 + "亿";
      } else if (value > 9999) {
        num = Math.floor(value / 1000) / 10 + "万";
      } else if (value < 9999 && value > -9999) {
        num = value;
      }
      return num;
    },
    // 处理播放时长
    getTemplateTimeByDate: function (date, template) {
      date = new Date(date);

      let TimeObj = {
        "Y+": date.getFullYear(),
        "(M+)": date.getMonth() + 1,
        "(d+)": date.getDate(),
        "(h+)": date.getHours(),
        "(m+)": date.getMinutes(),
        "(s+)": date.getSeconds(),
      };

      for (let k in TimeObj) {
        let reg = new RegExp(k);
        if (reg.test(template)) {
          let time = TimeObj[k]; // 判断当前模板时间是 两位 还是 一位的 // 如果是两位 个位数时间需要前面加零, 1 -> 01 // 如果是一位 不用加零操作
          if (RegExp.$1.length > 1) {
            time = time >= 10 ? time : "0" + time;
          }
          template = template.replace(reg, time);
        }
      }
      return template;
    },
  },
  watch: {
    searchactive(val, oval) {
      if (val != oval) {
        this.getsearchdata();
      }
    },
  },
  methods: {
    getsearchdata() {
      if (this.active == 0) {
        this.getsearchalldata(1);
      } else if (this.active == 1) {
        this.getsearchalldata(100);
      } else if (this.active == 2) {
        this.getsearchalldata(10);
      } else if (this.active == 3) {
        this.getsearchalldata(1000);
      }
    },
    getsearchalldata(type) {
      this.axios({
        method: "GET",
        url: "/search",
        params: {
          keywords: this.searchactive,
          limit: 30,
          type,
        },
      })
        .then((result) => {
          if (result.data.code == 200) {
            if (this.active == 0) {
              this.getdatas = result.data.result.songs;
            } else if (this.active == 1) {
              this.getdatas = result.data.result.artists;
            } else if (this.active == 2) {
              this.getdatas = result.data.result.albums;
            } else if (this.active == 3) {
              this.getdatas = result.data.result.playlists;
            }
            this.isShow = this.getdatas.length > 0 ? true : false
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    //点击播放歌曲
    setStateCount(item) {
        this.playmusicids = this.getdatas.map(v => {
        return v.id
      })
      this.$store.commit("setplaymusicids",this.playmusicids)
      this.$store.commit("setmusicid",item.id)
    },
    //查看歌手
    goSingerDetail(item){
      this.$router.push({path:'singerDetail',query:{id:item.id}})
    },
    //前往专辑详情页面
    goSongAlbumDetail(item){
      this.$router.push({path:'songAlbumDetails',query:{id:item.id}})
    },
    //前往歌单详情页面
    goSongListDetail(item){
      this.$router.push({path:'songListDetails',query:{id:item.id}})
    }
  },
};
</script>

<style lang="scss" scoped>
</style>